@import url(https://fonts.googleapis.com/css?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:'Bebas Neue', cursive}body{background:#e3aa84 !important}div[id='d-flex justify-content-center']{display:flex;align-items:center;justify-content:center;height:100vh}div[id='d-flex justify-content-center'] #innerContainer{width:900px;border:6px solid black;padding:8px;border-style:double;box-shadow:10px 11px 0px -3px rgba(0,0,0,0.75)}div[id='d-flex justify-content-center'] #innerContainer #buttonContainer{display:flex}div[id='d-flex justify-content-center'] #innerContainer #buttonContainer #tweet-quote{margin-left:auto}div[id='d-flex justify-content-center'] #innerContainer #buttonContainer #tweet-quote svg{width:40px;height:40px}div[id='d-flex justify-content-center'] #innerContainer #buttonContainer .button{padding:16px 42px;box-shadow:0px 0px 12px -2px rgba(0,0,0,0.5);line-height:1.25;background:#d16c00;text-decoration:none;color:#000;font-size:16px;letter-spacing:.08em;text-transform:uppercase;position:relative;-webkit-transition:background-color .6s ease;transition:background-color .6s ease;overflow:hidden;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}div[id='d-flex justify-content-center'] #innerContainer #buttonContainer .button:after{content:"";position:absolute;width:0;height:0;top:50%;left:50%;-webkit-transform-style:flat;transform-style:flat;-webkit-transform:translate3d(-50%, -50%, 0);transform:translate3d(-50%, -50%, 0);background:rgba(255,255,255,0.1);border-radius:100%;-webkit-transition:width .3s ease, height .3s ease;transition:width .3s ease, height .3s ease}div[id='d-flex justify-content-center'] #innerContainer #buttonContainer .button:focus,div[id='d-flex justify-content-center'] #innerContainer #buttonContainer .button:hover{background:#ad5a00}div[id='d-flex justify-content-center'] #innerContainer #buttonContainer .button:active:after{width:200px;height:200px}div[id='d-flex justify-content-center'] #innerContainer #quoteContainer{min-height:300px;position:relative}div[id='d-flex justify-content-center'] #innerContainer #quoteContainer #quote{font-size:40px}div[id='d-flex justify-content-center'] #innerContainer #author{font-size:50px;text-align:right;margin-right:10px;text-decoration:underline;margin-left:auto}@media screen and (max-width: 550px){div[id='d-flex justify-content-center']{height:auto}#quoteContainer #quote{font-size:20px}#author{font-size:30px;text-align:right;margin-right:10px;text-decoration:underline;margin-left:auto}}

